.Pagination {
  display: flex;
  padding: 20px;

  border-radius: 35px;
  box-shadow: 2px 0 10px #d3d3d3;
}

.pagination__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  list-style: none;
  margin: 0;
  padding: 0;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;

  margin: 7px 7px;
  min-width: 30px;
  min-height: 30px;

  color: #e23737;
  text-decoration: none;
  background-color: #fff;

  border: none;
  border-radius: 15px;
  cursor: pointer;
  box-shadow: 0 3px 6px #ff8080;
  transition: all 0.5s;
}

.button--flip {
  font-weight: bold;
}

.button:hover {
  color: #fff;
  background-color: #e23737;
}

.button--active {
  color: #fff;
  background-color: #e23737;
}

.button--multiple-dots::after {
  content: '...';
  margin: 7px 7px;
  width: 30px;
  height: 10px;

  color: #e23737;
  background-color: #fff;
  border: none;
  border-radius: 15px;
  cursor: pointer;
  box-shadow: 0 3px 6px #ff8080;
  transition: all 0.5s;
}

.isDisabled {
  cursor: not-allowed;
  color: #b4b4b4;
  background-color: rgb(250, 250, 250);
  box-shadow: 0 2px 3px #e9e6e6;
}

.isDisabled:hover {
  cursor: not-allowed;
  color: #b4b4b4;
  background-color: rgb(250, 250, 250);
  box-shadow: 0 2px 3px #e9e6e6;
}

.dots {
  color: #ffc6c6;
  font-weight: bold;
}

.hide {
  display: none;
}
